<template>
  <div>
    <div class="reply-wrapper" :class="{'reply-wrapper--me': isMe }" @click="clickHandler">
      <div class="reply-sender">{{ repliedCommentSender }}</div>
      <image-loader v-if="comment.isAttachment(comment.payload.replied_comment_message)"
        :comment="comment"
        :message="comment.payload.replied_comment_message"
        :on-click-image="onClickImage"
        :callback="callback">
      </image-loader>
      <div v-if="!comment.isAttachment(comment.payload.replied_comment_message)"
        v-html="repliedCommentMessage">
      </div>
    </div>
    <image-loader v-if="comment.isAttachment(comment.payload.text)"
      :comment="comment"
      :message="comment.payload.text"
      :on-click-image="onClickImage"
      :callback="callback">
    </image-loader>
    <div class="qcw-comment__content"
      v-if="!comment.isAttachment(comment.payload.text)"
    >
      {{repliedCommentText}}
    </div>
  </div>
</template>

<script>
import ImageLoader from './ImageLoader.vue';
export default {
  name: 'CommentReply',
  components: {ImageLoader},
  props: ['comment', 'isMe', 'clickHandler', 'repliedCommentSender', 'onClickImage', 'callback', 'repliedCommentMessage', 'repliedCommentText'],
  mounted() {
    console.group('[comp:comment-reply]')
    console.log('comment', this.comment)
    console.log('isMe', this.isMe)
    console.log('repliedCommentSender', this.repliedCommentSender)
    console.log('repliedCommentMessage', this.repliedCommentMessage)
    console.log('repliedCommentText', this.repliedCommentText)
    console.log('isAttachment text', this.comment.isAttachment(this.comment.payload.text))
    console.log('isAttachment replied-comment-message', this.comment.isAttachment(this.comment.payload.replied_comment_message))
    console.groupEnd('[comp:comment-reply]')
  }
}
</script>




